    margin-bottom: 10px;
    padding: 10px 20px;
}

.table-striped tbody tr:nth-of-type(odd) {
    background-color: rgb(243, 246, 249);
}

.header-title {
    font-size: 18px;
    padding-bottom: 15px;
    margin-bottom: 15px;
    position: relative;
    z-index: 1;
    font-weight: 700;
    text-transform: capitalize;
}

.header-title::after {
    content: "";
    background-color: #3a80e5;
    width: 38px;
    height: 3px;
    border-radius: 5px;
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0px;
}

.modal-example {
    position: relative;
    top: auto;
    right: auto;
    bottom: auto;
    left: auto;
    z-index: 1;
    display: block;
}